import { Fragment } from 'vue-frag';
import NcActionButton from '@nextcloud/vue/dist/Components/NcActionButton.js';
import NcActionInput from '@nextcloud/vue/dist/Components/NcActionInput.js';
import NcAppNavigationItem from '@nextcloud/vue/dist/Components/NcAppNavigationItem.js';
import NcButton from '@nextcloud/vue/dist/Components/NcButton.js';
import NcCounterBubble from '@nextcloud/vue/dist/Components/NcCounterBubble.js';
import NcModal from '@nextcloud/vue/dist/Components/NcModal.js';
import NcNoteCard from '@nextcloud/vue/dist/Components/NcNoteCard.js';
import AccountGroup from 'vue-material-design-icons/AccountGroup.vue';
import Delete from 'vue-material-design-icons/Delete.vue';
import Pencil from 'vue-material-design-icons/Pencil.vue';
import { showError } from '@nextcloud/dialogs';
export default {
  name: 'GroupListItem',
  components: {
    AccountGroup,
    Delete,
    Fragment,
    NcActionButton,
    NcActionInput,
    NcAppNavigationItem,
    NcButton,
    NcCounterBubble,
    NcModal,
    NcNoteCard,
    Pencil
  },
  props: {
    /**
     * If this group is currently selected
     */
    active: {
      type: Boolean,
      required: true
    },
    /**
     * Number of members within this group
     */
    count: {
      type: Number,
      default: null
    },
    /**
     * Identifier of this group
     */
    id: {
      type: String,
      required: true
    },
    /**
     * Name of this group
     */
    name: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      loadingRenameGroup: false,
      openGroupMenu: false,
      showRemoveGroupModal: false
    };
  },
  computed: {
    settings() {
      return this.$store.getters.getServerData;
    }
  },
  methods: {
    handleGroupMenuOpen() {
      this.openGroupMenu = true;
    },
    async renameGroup(gid) {
      // check if group id is valid
      if (gid.trim() === '') {
        return;
      }
      const displayName = this.$refs.displayNameInput.$el.querySelector('input[type="text"]').value;

      // check if group name is valid
      if (displayName.trim() === '') {
        return;
      }
      try {
        this.openGroupMenu = false;
        this.loadingRenameGroup = true;
        await this.$store.dispatch('renameGroup', {
          groupid: gid.trim(),
          displayName: displayName.trim()
        });
        this.loadingRenameGroup = false;
      } catch {
        this.openGroupMenu = true;
        this.loadingRenameGroup = false;
      }
    },
    async removeGroup() {
      try {
        await this.$store.dispatch('removeGroup', this.id);
        this.showRemoveGroupModal = false;
      } catch (error) {
        showError(t('settings', 'Failed to remove group "{group}"', {
          group: this.name
        }));
      }
    }
  }
};